import { mergeAll } from 'ramda';
import { html, render } from 'lit-html';
import { getResultFromOptions } from '../helpers';
import { configForm, controlValue, controlMin, controlMax, controlStep, controlOrientation, controlTooltips, controlTooltipFormat, controlIntervals, controlGrid, controlGridFormat, } from '../components';
import { defaultOptions, RangeSlider } from '../../src/range-slider';
class Example {
    constructor(id, options = defaultOptions) {
        this.options = mergeAll([defaultOptions, options]);
        // get container elements
        this.rootEl = document.getElementById(id);
        this.configEl = this.rootEl.querySelector('.config-panel');
        this.resultEl = this.rootEl.querySelector('.result-panel__output');
        this.rangeSliderEl = this.rootEl.querySelector('.result-panel__range-slider > input[type="range"]');
        // bind event handlers
        this.onFormUpdate = this.onFormUpdate.bind(this);
        this.onRangeSliderUpdate = this.onRangeSliderUpdate.bind(this);
        // init RangeSlider
        this.rs = new RangeSlider(this.rangeSliderEl, options);
        this.rs.on(RangeSlider.EVENT_UPDATE, this.onRangeSliderUpdate);
        this.render();
    }
    // eslint-disable-next-line class-methods-use-this
    getConfigPanelElements() {
        return [
            controlValue,
            controlMin,
            controlMax,
            controlStep,
            controlOrientation,
            controlTooltips,
            controlTooltipFormat,
            controlIntervals,
            controlGrid,
            controlGridFormat,
        ];
    }
    render() {
        const { options } = this;
        this.renderResult(options);
        this.renderConfigPanel(options);
    }
    onFormUpdate(proposal) {
        this.options = proposal(this.options);
        this.render();
        this.rs.set(this.options);
    }
    onRangeSliderUpdate(options) {
        this.options = options;
        this.render();
    }
    renderConfigPanel(options) {
        const { configEl, onFormUpdate } = this;
        const configPanel = configForm({ options, onUpdate: onFormUpdate }, this.getConfigPanelElements());
        render(html `
        ${configPanel}
      `, configEl);
    }
    renderResult(options) {
        render(html `
        ${getResultFromOptions(options)}
      `, this.resultEl);
    }
}
export default Example;
