import { partialRight } from 'ramda';
import moment from 'moment';
import Example from './example';
import { controlValue, controlMin, controlMax, controlStep, controlOrientation, controlTooltips, controlTooltipFormat, controlIntervals, controlGrid, controlGridFormat, } from '../components';
function timestampToISOString(timestamp) {
    return moment
        .unix(timestamp)
        .toISOString()
        .substr(0, 10);
}
function isoStringToTimestamp(isoString) {
    return moment(isoString).unix();
}
function dateToTimestamp(date) {
    return moment(date).unix();
}
const currentYear = new Date().getFullYear();
const oneDay = moment.duration(1, 'day').asSeconds();
const defaultOptions = {
    value: dateToTimestamp(new Date(currentYear, 2, 3)),
    min: dateToTimestamp(new Date(currentYear, 0, 1)),
    max: dateToTimestamp(new Date(currentYear, 11, 31)),
    step: oneDay,
    orientation: 'horizontal',
    cssClass: 'range-slider',
    tooltips: true,
    tooltipFormat: '{{date}}',
    intervals: false,
    grid: { isVisible: true, numCells: [4, 2, 5] },
    gridFormat: '{{date}}',
};
class ExampleDate extends Example {
    constructor(id, options = defaultOptions) {
        super(id, options);
    }
    // eslint-disable-next-line class-methods-use-this
    getConfigPanelElements() {
        return [
            partialRight(controlValue, [
                {
                    type: 'date',
                    valueFormatter: timestampToISOString,
                    valueParser: isoStringToTimestamp,
                },
            ]),
            partialRight(controlMin, [
                {
                    type: 'date',
                    valueFormatter: timestampToISOString,
                    valueParser: isoStringToTimestamp,
                },
            ]),
            partialRight(controlMax, [
                {
                    type: 'date',
                    valueFormatter: timestampToISOString,
                    valueParser: isoStringToTimestamp,
                },
            ]),
            partialRight(controlStep, [
                {
                    type: 'number',
                    valueFormatter: (timestamp) => timestamp / oneDay,
                    valueParser: (steps) => steps * oneDay,
                },
            ]),
            controlOrientation,
            controlTooltips,
            controlTooltipFormat,
            controlIntervals,
            controlGrid,
            controlGridFormat,
        ];
    }
}
export default ExampleDate;
